import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin', 'userId'])
	},
	data() {
		return {
			url: [
				'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/order/by/userId',
				'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/wait/payment',
				'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/success/order',
				'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/charge/back'
			], //下拉刷新
			add: [1, 1, 1, 1], //下拉刷新
			headerPosition: 'fixed',
			headerTop: '0px',
			orderState: {
				unpaid: '等待付款',
				completed: '交易已完成',
				refunds: '门票退换处理中',
				cancelled: '订单已取消'
			},
			orderType: ['全部', '待付款', '待评价', '退换货'],
			//订单列表 演示数据
			orderList: [[], [], [], []],
			list: [],
			tabbarIndex: 0
		};
	},
	onLoad(option) {
		//option为object类型，会序列化上个页面传递的参数
		console.log('option: ' + JSON.stringify(option));
		let tbIndex = parseInt(option.tbIndex) + 1;
		this.tabbarIndex = tbIndex;
		console.log(this.tabbarIndex);

		for (let i = 0; i < this.add.length; i++) {
			this.add[i] = 1;
		}
		let that = this;
		console.log(that.userId);
		uni.request({
			url: that.url[that.tabbarIndex],
			method: 'POST',
			data: {
				pageNum: that.add[that.tabbarIndex],
				userId: that.userId
			},
			success: res => {
				console.log(res.data);
				that.orderList[that.tabbarIndex] = res.data.data.orderResponseList;

				that.list = that.orderList[tbIndex];
			},
			fail: () => {
				uni.showLoading({
					title: '订单列表没有数据'
				});
				setTimeout(() => {
					uni.hideLoading();
				}, 500);
				return;
			},
			complete: () => {}
		});
		this.isSupport = !this.isSupport;
		//兼容H5下排序栏位置









	},
	onPageScroll(e) {
		return;
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
	},
	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
		//下拉刷新
		const pages = getCurrentPages();
		const page = pages[pages.length - 1];
		const currentWebview = page.$getAppWebview();
		currentWebview.setStyle({
			pullToRefresh: {
				support: !this.isSupport,
				style: plus.os.name === 'Android' ? 'circle' : 'default'
			}
		});
	},
	methods: {
		showList: function(tbIndex) {
			let that = this;
			console.log(that);
			this.tbIndex = tbIndex;
			//调用四个接口值

			uni.request({
				url: that.url[tbIndex],
				method: 'POST',
				data: {
					pageNum: that.add[tbIndex],
					userId: that.userId
				},
				success: res => {
					console.log(res.data.data==null)
					if (res.data.data!=null) {
						that.orderList[tbIndex] = res.data.data.orderResponseList;
						that.list = that.orderList[tbIndex];
						console.log(res.data.data);
					}else{
						uni.showToast({
							title: '订单列表没有数据'
						});
						setTimeout(() => {
							uni.hideToast();
						}, 1000);
					}
				},
				fail: () => {
					uni.showToast({
						title: '订单列表没有数据'
					});
					return 0;
				},
				complete: () => {}
			});

			return that.orderList[tbIndex];
		},
		showType(tbIndex) {
			this.tabbarIndex = tbIndex;
			this.list = [];
			console.log(this.list)
			console.log(tbIndex);
			//获取当前点击顶部导航栏数据
			setTimeout(() => {
				this.list = this.showList(tbIndex);
			}, 500);
		},
		cancelOrder(row) {
			uni.showModal({
				title: '取消订单',
				content: '确定取消此订单？',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.doCancelOrder(row.id);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		doCancelOrder(id) {
			console.log(id);
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/close/pay/order',
				method: 'PUT',
				data: {
					id: id
				},
				success: res => {
					console.log(res);
					uni.showToast({
						title: '取消成功'
					});
				},
				fail: () => {},
				complete: () => {}
			});
			// let typeNum = this.orderList.length;
			// for (let i = 0; i < typeNum; i++) {
			// 	let list = this.orderList[i];
			// 	let orderNum = list.length;
			// 	if (orderNum > 0 && list[0].type == 'unpaid') {
			// 		for (let j = 0; j < orderNum; j++) {
			// 			if (this.orderList[i][j].id == id) {
			// 				this.orderList[i][j].type = 'cancelled';
			// 				break;
			// 			}
			// 		}
			// 	}
			// }
		},
		toPayment(row) {
			uni.showLoading({
				title: '正在获取订单...'
			});
			let paymentOrder = [];
			paymentOrder.push(row);
			setTimeout(() => {
				uni.hideLoading();
				uni.navigateTo({
					url: '../../pay/payment/payment?amount=' + row
				});
			}, 500);
		},
		toGood(row) {
			console.log(row);
			uni.navigateTo({
				url: '../../details/goods?id=' + row
			});
		},
		toRefund(row) {
			console.log(row);
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/chargeBack/order',
				method: 'PUT',
				data: {
					id: row
				},
				success: res => {
					console.log(res);
					uni.showToast({
						title: '退票成功'
					});
				},
				fail: res => {
					uni.showToast({
						title: '出错了'
					});
				}
			});
		}
	},

	onReachBottom() {
		let that = this;
		that.add[that.tbIndex]++;
		uni.showToast({ title: '触发上拉刷新' });
		setTimeout(() => {
			uni.request({
				url: that.url[that.tbIndex],
				method: 'POST',
				data: {
					pageNum: that.add[that.tbIndex],
					userId: that.userId
				},
				success: res => {
					console.log(res);
					let scenicSpotPush = [];
					scenicSpotPush = res.data.data.orderResponseList;
					for (let i = 0; i < scenicSpotPush.length; i++) {
						that.list.push(scenicSpotPush[i]);
					}
					uni.hideLoading();
				},
				fail: () => {},
				complete: () => {}
			});
		}, 500);
	}
};
